//   ,  about
const buttonForText = document.getElementById("viewText");

//  ,     ,  about
const hiddenText = document.querySelector(".about__text");

//  ,     ,  about
const hiddenTextLaptop = document.querySelector(".about__text--hidden-inner");

//  ,     ,  about
const hiddenTextDesctop = document.querySelector(".about__text--hidden-inner-x2")

//   ,   ,  about
const iconForText = document.querySelector(".btn-visible__icon-text");

//    ,  services
const buttonForBrands = document.getElementById("viewBrands");

//   ,  services
const hiddenElementsBrands = document.querySelectorAll('.services__list-brands--item:nth-child(n + 5)');

//   , ,  services
const iconForBrands = document.querySelector('.btn-visible__icon-brands');

//    ,  repair
const buttonForRepair = document.getElementById('viewRepair');

//   ,  repair
const hiddenElementsRepair = document.querySelectorAll('.services__list-repair--item:nth-child(n + 4)')

//   ,   ,  repair
const iconForRepair = document.querySelector('.btn-visible__icon-repair')

//    ()
buttonForText.addEventListener('click' , function () {
    
    // hiddenText.classList.toggle("inner--hidden");
    hiddenText.classList.toggle('show');
    hiddenTextLaptop.classList.toggle('show');
    hiddenTextDesctop.classList.toggle('show');

    if (buttonForText.innerText === ' ') {
        buttonForText.innerText = 'C';
    } else {
        buttonForText.innerText = ' '
    }

  iconForText.classList.toggle('icon-rotate')

})


//      
buttonForBrands.addEventListener('click', function() {
    console.log('click 2');

    for (let i = 0; i < hiddenElementsBrands.length; i++) {
        hiddenElementsBrands[i].classList.toggle('show')
    }

    if (buttonForBrands.innerText === ' ') {
        buttonForBrands.innerText = 'C';
    } else {
        buttonForBrands.innerText = ' '
    }

    iconForBrands.classList.toggle('icon-rotate')
})

//      
buttonForRepair.addEventListener('click', function () {
    for (let i = 0; i < hiddenElementsRepair.length; i++) {

    hiddenElementsRepair[i].classList.toggle('show')
    }

    if (buttonForRepair.innerText === ' ') {
        buttonForRepair.innerText = 'C';
    } else {
        buttonForRepair.innerText = ' '
    }

    iconForRepair.classList.toggle('icon-rotate')
})